import stampit from 'stampit';
import { parse } from '@swagger-api/apidom-parser-adapter-yaml-1-2';
import { ParserError } from "../../../util/errors/index.js";
import Parser from "../Parser.js";
const YamlParser = stampit(Parser, {
  props: {
    name: 'yaml-1-2'
  },
  methods: {
    canParse(file) {
      return ['text/yaml', 'application/yaml'].includes(file.mediaType) || ['.yaml', '.yml'].includes(file.extension);
    },

    async parse(file) {
      const source = Buffer.isBuffer(file.data) ? file.data.toString() : file.data;

      try {
        return await parse(source, {
          sourceMap: this.sourceMap
        });
      } catch (error) {
        throw new ParserError(`Error parsing "${file.uri}"`, error);
      }
    }

  }
});
export default YamlParser;